




























































import md5 from "@/common/SDK/md5.min.js";
import general from "@/common/utils/general.js"
import Vue from 'vue'
export default {
	data() {
		return {
			phoneNumber:"", //手机号
			code:'', //验证码
			passwd:"", //密码
			passwd1:"", //确认密码
			getCodeText:'获取验证码',
			getCodeBtnColor:"#ffffff",
			getCodeisWaiting:false
		}
	},
	onLoad() {

	},
	methods: {
		Timer(){},
		async checkCode (){
			console.log(123)
			let result = await general.checkPhoneCode(this.global.baseUrl,this.phoneNumber,this.code);
			// console.log(result)
			if(!result){
				wx.showToast({
					title:'验证码错误',
					icon:'none'
				})
			}
		},
		getCode(){
			uni.hideKeyboard()
			if(this.getCodeisWaiting){
				return ;
			}
			if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phoneNumber))){ 
				uni.showToast({title: '请填写正确手机号码',icon:"none"});
				return false; 
			} 
			this.getCodeText = "发送中..."
			this.getCodeisWaiting = true;
			this.getCodeBtnColor = "rgba(255,255,255,0.5)"
			//示例用定时器模拟请求效果
			// setTimeout(()=>{
			// 	uni.showToast({title: '验证码已发送',icon:"none"});
			// 	//示例默认1234，生产中请删除这一句。
			// 	this.code=1234;
			// 	this.setTimer();
			// },1000)
			uni.request({
				url:this.global.baseUrl+'mobile/getCode',
				method:'get',
				data:{
					phone:'13983171812',
					type:'rege'
				},
				
				success: (res) => {
					console.log(res)
					if(res.data=='OK'){
						this.setTimer()
					}
				}
			})
		},
		setTimer(){
			let holdTime = 5;
			this.getCodeText = "重新获取(60)"
			this.Timer = setInterval(()=>{
				if(holdTime<=0){
					this.getCodeisWaiting = false;
					this.getCodeBtnColor = "#ffffff";
					this.getCodeText = "获取验证码"
					clearInterval(this.Timer);
					return ;
				}
				this.getCodeText = "重新获取("+holdTime+")"
				holdTime--;
				
			},1000)
		},
		async doReg(){
			uni.hideKeyboard()
			//手机格式验证
			if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phoneNumber))){ 
				uni.showToast({title: '请填写正确手机号码',icon:"none"});
				return false; 
			} 
			//验证码
			if(this.code==''){ 
				uni.showToast({title: '请输入验证码',icon:"none"});
				return false; 
			}
			if(this.passwd==''){ 
				uni.showToast({title: '请输入密码',icon:"none"});
				return false; 
			}
			if(this.passwd1==''){ 
				uni.showToast({title: '请输入确认密码',icon:"none"});
				return false; 
			}
			if(this.passwd!==this.passwd1){ 
				uni.showToast({title: '两次密码不一致',icon:"none"});
				return false; 
			}
			let result = await general.checkPhoneCode(this.global.baseUrl,this.phoneNumber,this.code);
			console.log(result)
			if(!result){
				uni.showToast({title: '验证码错误',icon:"none"});
				return false;
			}
			uni.showLoading({
				title: '提交中...'
			})
			//发起注册请求。
			uni.request({
				url:this.global.baseUrl+'regeUser',
				method:'GET',
				data:{
					phone:this.phoneNumber,
					pwd:this.passwd,
					method:1
				},
				success: (res) => {
					console.log(res)
					var str=''
					if(res.data=='OK'){
						str='注册成功';
						setTimeout(()=>{
							this.toLogin()
						},200)
					}
					if(res.data=='keyErr'){
						str='手机号错误或已被注册'
					}
					wx.showToast({
						title:str,
						icon:'none'
					})
					
					wx.hideLoading()
				}
			})
		},
		toLogin(){ 
			uni.hideKeyboard()
			uni.redirectTo({url: 'login'});
			uni.navigateBack();
		}
	}
}
